<template>
  <div class="bigbox">
    <table>
      <thead v-if="currentWeek!=6 && currentWeek>=1">
        <tr class="title">
          <td colspan="8">22q计科8班&nbsp;&nbsp;2024-2025学年秋课程表</td>
        </tr>
        <tr class="title">
          <td colspan="8">{{today}}&nbsp;&nbsp;本周是第 {{ currentWeek }} 教学周</td>
        </tr>
        <tr >
          <td>节次/时间</td>
          <td>星期一</td>
          <td>星期二</td>
          <td>星期三</td>
          <td>星期四</td>
          <td>星期五</td>
          <td>星期六</td>
          <td>星期日</td>
        </tr>
      </thead>
      <tbody v-if="currentWeek==6  && currentWeek>=1" ><tr><td style="font-size: 100px;" colspan="7" rowspan="5">本周没有安排课程</td></tr></tbody>
      <tbody v-if="currentWeek<1" ><tr><td style="font-size: 100px;" colspan="7" rowspan="5">当前时间还没有开学哦！</td></tr></tbody>
      <tbody v-if="currentWeek!=6 && currentWeek>=1">
        <tr v-for="(a) in classtime" :key="a.id">
          <td>{{ a.num }}<br>{{ a.times }}</td>
          <td v-for="(day, index) in 7" :key="index">
            <div v-for="(course) in getCourses(index + 1, a.id)" :key="course.id">
              {{ course.name }}<br>{{ course.classroom }}
            </div>
          </td>
        </tr>

      </tbody>
    </table>
  </div>
</template>
<script>

export default {
  data () {
    return {
      currentWeek: '',
      today: '',
      classtime: {
        first: { id: 1, num: '第一讲', times: '8:30-10:05' },
        second: { id: 2, num: '第二讲', times: '10:20-11:55' },
        third: { id: 3, num: '第三讲', times: '13:30-15:05' },
        forth: { id: 4, num: '第四讲', times: '15:20-16:55' },
        fifth: { id: 5, num: '第五讲', times: '18:30-20:05' }
      },
      classdata: [
        { id: 1, name: '环境导视与展示三维建模', clssadd: [[1, 1], [3, 3], [5, 2]], classweek: [[1, 18], [1, 18], [1, 2]], classroom: [3304, 3304, 3504] },
        { id: 2, name: '移动终端设计与开发', clssadd: [[1, 2]], classweek: [[1, 17]], classroom: [1103] },
        { id: 3, name: 'Java网络编程', clssadd: [[1, 3], [4, 3]], classweek: [[1, 9], [1, 9]], classroom: [5102, 5102] },
        { id: 4, name: '数据结构', clssadd: [[1, 4], [3, 2]], classweek: [[1, 15], [1, 15]], classroom: [5103, 5202] },
        { id: 5, name: 'Linux操作系统', clssadd: [[2, 2], [7, 3]], classweek: [[1, 16], [4, 15]], classroom: [5102, 5101] },
        { id: 6, name: '动态效果设计与实验', clssadd: [[2, 3], [3, 4], [4, 2]], classweek: [[1, 18], [1, 7], [1, 18]], classroom: [3504, 3504, 3504] },
        { id: 7, name: 'Unity3D游戏开发', clssadd: [[3, 1], [5, 3]], classweek: [[1, 11], [1, 11]], classroom: [6206, 6105] },
        { id: 8, name: 'PC机常用软件', clssadd: [[4, 1]], classweek: [[4, 12]], classroom: [5302] },
        { id: 9, name: '形势与政策', clssadd: [[6, 4]], classweek: [[7, 10]], classroom: [2103] },
        { id: 10, name: '专业英语', clssadd: [[7, 1]], classweek: [[4, 12]], classroom: [2101] }

      ]
    }
  },
  methods: {
    isInWeekRange (weekRange, currentWeek) {
      return currentWeek >= weekRange[0] && currentWeek <= weekRange[1]
    },
    getCourses (day, classTimeId) {
      const result = []
      this.classdata.forEach(course => {
        course.clssadd.forEach((time, index) => {
          if (time[0] === day && time[1] === classTimeId && this.isInWeekRange(course.classweek[index], this.currentWeek)) {
            result.push({
              id: course.id,
              name: course.name,
              classroom: course.classroom[index]
            })
          }
        })
      })
      return result
    },
    nowtime () {
      this.settime = setInterval(() => {
        const NowTime = new Date()
        const year = NowTime.getFullYear()
        const month = NowTime.getMonth() < 10 ? '0' + NowTime.getMonth() : NowTime.getMonth()
        const date = NowTime.getDate() < 10 ? '0' + NowTime.getDate() : NowTime.getDate()
        const hour = NowTime.getHours() < 10 ? '0' + NowTime.getHours() : NowTime.getHours()
        const minutes = NowTime.getMinutes() < 10 ? '0' + NowTime.getMinutes() : NowTime.getMinutes()
        const second = NowTime.getSeconds() < 10 ? '0' + NowTime.getSeconds() : NowTime.getSeconds()
        this.today = `${year}年${month}月${date}日${hour}时${minutes}分${second}秒`
      }, 1000)
    }
  },
  created () {
    const NowTime = new Date()
    const BeginTime = new Date('2024-08-28 00:00:00')
    const TimeDiff = NowTime.getTime() - BeginTime.getTime()
    const WeeksPassed = Math.floor(TimeDiff / (1000 * 60 * 60 * 24 * 7))
    this.currentWeek = WeeksPassed
    this.nowtime()
  },
  beforeDestroy () {
    clearInterval(this.settime)
  }

}
</script>

