import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '@/views/HomeView.vue'
import ClassView from '@/views/ClassView.vue'
import Nofind from '@/views/NoFind.vue'
import WebApp from '@/views/WebApp.vue'
import WebFile from '@/views/WebFile.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/classtable',
    name: 'classtable',
    component: ClassView
  },
  {
    path: '/WebApp',
    name: 'WebApp',
    component: WebApp
  },
  {
    path: '/WebFile',
    name: 'WebFile',
    component: WebFile
  },
  {
    path: '/*',
    name: 'Nofind',
    component: Nofind
  }

]

const router = new VueRouter({
  routes
})

export default router
